import {
  Form,
  Button,
  Modal,
  Table,
  TableBody,
  TableRow,
  TableCell,
  Input,
} from "semantic-ui-react";
import DishSelector from "./DishSelector";
import CateSelector from "./CateSelector";

export default function EditForm({ columns, state, dispatch, row, setRow }) {
  // console.log(state);
  // 
  columns = columns.filter((col) => col.editable);
  //  group
  const formGroups = (columnsPerRow) => {
    const groups = [];
    for (let i = 0; i < columns.length; i++) {
      if (i % columnsPerRow == 0)
        groups.push(
          <Form.Group widths={columnsPerRow} key={i}>
            {formFields(i, columnsPerRow)}
          </Form.Group>
        );
    }
    return groups;
  };

  const handleInputChange = (e) => {
    setRow({ ...row, [e.target.name]: e.target.value });
  };

  // 
  const handleDishChange = (e, { value }) => {
    setRow({ ...row, dish: value });
  };

  const handleCateChange = (e, { value }) => {
    setRow({ ...row, cate: value });
  };

  //  onAddItem
  const handleAddition = (e, { value }) => {
    dispatch({ type: "ADD_OPTION", value });
  };

  //  group  field
  const formFields = (index, columnsPerRow) => {
    let fields = [];
    columns.slice(index, index + columnsPerRow).map((col, index) => {
      // 
      switch (col.dataKey) {
        case "dish":
          fields.push(
            <Form.Field key={index}>
              <label>{col.title}</label>
              <DishSelector
                onAddItem={handleAddition}
                options={state.options}
                value={row.dish}
                onChange={handleDishChange}
              />
            </Form.Field>
          );
          return;

        case "cate":
          fields.push(
            <Form.Field key={index}>
              <label>{col.title}</label>
              <CateSelector
                onAddItem={handleAddition}
                options={state.cates}
                value={row.cate}
                onChange={handleCateChange}
              />
            </Form.Field>
          );
          return;

        default:
          fields.push(
            <Form.Field key={index}>
              <label>{col.title}</label>
              <input
                type={col.type}
                name={col.dataKey}
                value={row[col.dataKey]}
                onChange={handleInputChange}
              />
            </Form.Field>
          );
      }
    });
    return fields;
  };

  const tableForm = () => {
    return (
      <Table definition unstackable>
        <TableBody>
          {columns.map((col) => {
            return (
              <TableRow>
                <TableCell width={3}>{col.title}</TableCell>
                <TableCell>
                  <Input
                    type={col.type}
                    name={col.dataKey}
                    value={row[col.dataKey]}
                    onChange={handleInputChange}
                    fluid
                  />
                </TableCell>
              </TableRow>
            );
          })}

          {/* <TableRow>
            <TableCell></TableCell>
            <TableCell>
              <Input type="text" name="workName" fluid />
            </TableCell>
          </TableRow>
          <TableRow>
            <TableCell width={3}></TableCell>
            <TableCell>
              <Input type="text" name="workNote" fluid />
            </TableCell>
          </TableRow> */}
        </TableBody>
      </Table>
    );
  };

  return (
    <>
      <Modal
        onClose={() => dispatch({ type: "CLOSE_EDITFORM" })}
        open={state.isEditFormOpen}
        closeIcon
      >
        <Modal.Header></Modal.Header>
        <Modal.Content>
          {/* <Form>{formGroups(2)}</Form> */}
          <Form>{tableForm()}</Form>
        </Modal.Content>
        <Modal.Actions>
          <Button
            primary
            onClick={() =>
              dispatch({
                type: state.editedRowIndex == -1 ? "CREATE" : "UPDATE",
                payload: { row },
              })
            }
          >
            
          </Button>
          <Button
            floated="left"
            color="red"
            onClick={() => {
              if (!confirm("?")) return;
              dispatch({ type: "DELETE", payload: { id: row.id } });
            }}
          >
            
          </Button>
        </Modal.Actions>
      </Modal>
    </>
  );
}
