import {
  ModalHeader,
  ModalDescription,
  ModalContent,
  ModalActions,
  Button,
  Header,
  Modal,
  TableBody,
  Table,
  TableCell,
  TableRow,
  Input,
} from "semantic-ui-react";

import CustSelect from "../../../../components/dropdown/CustSelect";

export default function EditForm({ row, setRow, setOpen, state, dispatch }) {
  // console.log(state);
  // return;
  const handleInputChange = (e) => {
    setRow({ ...row, [e.target.name]: e.target.value });
  };
  const handleCustChange = (e, { value }) => {
    // console.log(value)
    setRow({ ...row, custID: value, custName: e.target.innerText });
  };
  const save = () => {
    dispatch({ type: "UPDATE_MASTER", payload: { row } });
  };
  return (
    <div>
      <Modal
        // onClose={() => dispatch({ type: "CLOSE_MASTERFORM" })}
        open={state.isMasterEditFormOpen}
        // trigger={<Button>Show Modal</Button>}
      >
        <ModalHeader>
          <Header>{row?.quoteID}</Header>
        </ModalHeader>
        <ModalContent>
          <Table definition>
            <TableBody>
              <TableRow>
                <TableCell width={4}></TableCell>
                <TableCell colSpan="3">
                  <CustSelect onChange={handleCustChange} value={row?.custID} />
                </TableCell>
              </TableRow>
              <TableRow>
                <TableCell width={4}></TableCell>
                <TableCell colSpan="3">
                  <Input
                    onChange={handleInputChange}
                    value={row?.contactor}
                    type="text"
                    name="contactor"
                    fluid
                  />
                </TableCell>
              </TableRow>
              <TableRow>
                <TableCell width={4}></TableCell>
                <TableCell colSpan="3">
                  <Input
                    onChange={handleInputChange}
                    value={row?.caseNo}
                    type="text"
                    name="caseNo"
                    fluid
                  />
                </TableCell>
              </TableRow>
            </TableBody>
          </Table>

          {/* <ModalDescription>
             <Header>{data?.custName}</Header>
              <Header>{data?.contactor}</Header>
            <p></p>
          </ModalDescription> */}
        </ModalContent>
        <ModalActions>
          <Button
            floated="left"
            color="black"
            onClick={() => dispatch({ type: "CLOSE_MASTEREDITFORM" })}
          >
            
          </Button>
          <Button content="" onClick={save}  positive />
        </ModalActions>
      </Modal>
    </div>
  );
}
