import {
  ModalHeader,
  ModalDescription,
  ModalContent,
  ModalActions,
  Header,
  Button,
  Modal,
  TableBody,
  Table,
  TableCell,
  TableRow,
  Input,
} from "semantic-ui-react";

export default function EditForm({
  row,
  setRow,
  setOpen,
  data,
  state,
  dispatch,
}) {
  const handleInputChange = (e) => {
    setRow({ ...row, [e.target.name]: e.target.value });
  };

  const save = (index) => {
    console.log(state.editedRowIndex);
    if (state.editedRowIndex == -1) {
      dispatch({ type: "CREATE_DETAIL", payload: { row } });
    } else {
      dispatch({ type: "UPDATE_DETAIL", payload: { row, index } });
    }
  };

  const destroy = () => {
    if (!confirm("?")) return;
    dispatch({ type: "DESTROY_DETAIL", payload: { row } });
  };

  return (
    <div>
      <Modal
        onClose={() => dispatch({ type: "CLOSE_DETAILFORM" })}
        // onOpen={() => setOpen(true)}
        open={state.isDetailFormOpen}
        // trigger={<Button>Show Modal</Button>}
      >
        <ModalHeader>
          <Header></Header>
        </ModalHeader>
        <ModalContent>
          <Table definition>
            <TableBody>
              <TableRow>
                <TableCell></TableCell>
                <TableCell colSpan="3">
                  <Input
                    type="text"
                    name="workName"
                    value={row.workName}
                    onChange={handleInputChange}
                    fluid
                  />
                </TableCell>
              </TableRow>
              <TableRow>
                <TableCell></TableCell>
                <TableCell width={4}>
                  <Input
                    label=""
                    type="text"
                    name="size1"
                    value={row.size1}
                    onChange={handleInputChange}
                    fluid
                  />
                </TableCell>

                <TableCell width={4}>
                  <Input
                    type="text"
                    name="size2"
                    value={row.size2}
                    onChange={handleInputChange}
                    fluid
                  />
                </TableCell>
                <TableCell width={4}>
                  <Input
                    type="text"
                    name="size3"
                    value={row.size3}
                    onChange={handleInputChange}
                    fluid
                  />
                </TableCell>
              </TableRow>
              <TableRow>
                <TableCell width={4}></TableCell>
                <TableCell colSpan="3">
                  <Input
                    type="number"
                    name="qty"
                    value={row.qty}
                    onChange={handleInputChange}
                    fluid
                  />
                </TableCell>
              </TableRow>
              <TableRow>
                <TableCell width={4}></TableCell>
                <TableCell colSpan="3">
                  <Input
                    type="number"
                    name="price"
                    value={row.price}
                    onChange={handleInputChange}
                    fluid
                  />
                </TableCell>
              </TableRow>

              <TableRow>
                <TableCell width={4}></TableCell>
                <TableCell colSpan="3">
                  <Input
                    type="number"
                    name="donePrice"
                    value={row.donePrice}
                    onChange={handleInputChange}
                    fluid
                  />
                </TableCell>
              </TableRow>
              <TableRow>
                <TableCell width={4}></TableCell>
                <TableCell colSpan="3">
                  <Input
                    type="text"
                    name="workNote"
                    value={row.workNote}
                    onChange={handleInputChange}
                    fluid
                  />
                </TableCell>
              </TableRow>
            </TableBody>
          </Table>
        </ModalContent>
        <ModalActions>
          <Button floated="left" color="red" onClick={destroy}>
            
          </Button>
          <Button content="" onClick={save} positive />
        </ModalActions>
      </Modal>
    </div>
  );
}
