import React, { useState, useEffect } from "react";
import useAsyncReducer from "../../utils/asyncReducer";
import schema from "./data/schema.json";
import { reducer } from "./data/reducer";
// import { reducer } from "./data/reducerPdo";
import TableView from "./components/TableView";
import EditForm from "./components/EditForm";

import SearchBar from "./components/SearchBar";
import ViewTab from "./components/ViewTab";

export default function index() {
  // 
  const initState = {
    data: [], //
    options: [],
    cates:[],
    loading: true,
  };

  const [state, dispatch] = useAsyncReducer(reducer, initState);

  useEffect(() => {
    // 
    dispatch({ type: "LOAD", params: {} });
  }, []);

  // 
  const defaultRow = {};
  const { columns } = schema;
  columns.map((obj) => {
    defaultRow[obj.dataKey] = "";
  });

  // 
  defaultRow.date = new Date().toISOString().substring(0, 10);

  //  row  useAsyncReducer 
  // 
  const [row, setRow] = useState(defaultRow);

  const handleAdd = () => {
    dispatch({ type: "ADD" });
    setRow(defaultRow);
  };

  const handleEdit = (row, index) => {
    dispatch({ type: "EDIT", payload: { index } });
    console.log(row);
    setRow(row);
  };

  const handleChange = (e, { value }) => {
    // console.log(value);
    dispatch({ type: "LOAD", params: { workName: value } });
  };

  return (
    <>
      <SearchBar state={state} dispatch={dispatch} />
      <ViewTab
        state={state}
        dispatch={dispatch}
        columns={columns}
        handleAdd={handleAdd}
        handleEdit={handleEdit}
      />

      {/* {Array.isArray(state.data) && (
        <TableView
          state={state}
          dispatch={dispatch}
          columns={columns}
          handleAdd={handleAdd}
          handleEdit={handleEdit}
        />
      )} */}

      <EditForm
        columns={columns}
        row={row}
        setRow={setRow}
        state={state}
        dispatch={dispatch}
      />
    </>
  );
}
