import {
  FormField,
  Button,
  Checkbox,
  Form,
  TableBody,
  Table,
  TableCell,
  TableRow,
  Input,
} from "semantic-ui-react";
export default function Basic() {
  return (
    <Table definition>
      <TableBody>
        <TableRow>
          <TableCell width={4} ></TableCell>
          <TableCell colSpan="3">
            <Input type="text" name="workName" fluid />
          </TableCell>
        </TableRow>
        <TableRow>
          <TableCell width={4}></TableCell>
          <TableCell colSpan="3">
            <Input type="text" name="workNote" fluid />
          </TableCell>
        </TableRow>

        <TableRow>
          <TableCell width={4}></TableCell>
          <TableCell width={5}>
            <Input label="" name="size1" fluid placeholder="" />
          </TableCell>
          <TableCell>
            <Input name="size2" fluid placeholder="" />
          </TableCell>
          <TableCell>
            <Input name="size3" fluid placeholder="" />
          </TableCell>
        </TableRow>
      </TableBody>
    </Table>
  );
}
