import { Form, Button, Modal, Dropdown } from "semantic-ui-react";
import DropdownCom from "./DropdownCom";

export default function EditForm({ columns, state, dispatch, row, setRow }) {
  // console.log(state);
  // 
  columns = columns.filter((col) => col.editable);
  //  group
  const formGroups = (columnsPerRow) => {
    const groups = [];
    for (let i = 0; i < columns.length; i++) {
      if (i % columnsPerRow == 0)
        groups.push(
          <Form.Group widths={columnsPerRow} key={i}>
            {formFields(i, columnsPerRow)}
          </Form.Group>
        );
    }
    return groups;
  };

  const handleInputChange = (e) => {
    setRow({ ...row, [e.target.name]: e.target.value });
  };

  // 
  const handleWorkNameChange = (e, { value }) => {
    setRow({ ...row, workName: value });
  };

  //  onAddItem
  const handleAddition = (e, { value }) => {
    dispatch({ type: "ADD_OPTION",  value });
  };

  //  group  field
  const formFields = (index, columnsPerRow) => {
    let fields = [];
    columns.slice(index, index + columnsPerRow).map((col, index) => {
      // 
      switch (col.dataKey) {
        case "workName":
          fields.push(
            <Form.Field key={index}>
              <label>{col.title}</label>
              <DropdownCom
                onAddItem={handleAddition}
                options={state.options}
                value={row.workName}
                onChange={handleWorkNameChange}
              />
              {/* <Dropdown
                selection
                search
                onChange={handlePlayerChange}
                placeholder=""
                options={players}
                value={row.player}
              /> */}
            </Form.Field>
          );
          return;

        case "role":
          fields.push(
            <Form.Field key={index}>
              <label>{col.title}</label>
              <Dropdown
                selection
                search
                onChange={handleRoleChange}
                placeholder=""
                options={roles}
                value={row.role}
              />
            </Form.Field>
          );
          return;

        default:
          fields.push(
            <Form.Field key={index}>
              <label>{col.title}</label>
              <input
                type={col.type}
                name={col.dataKey}
                value={row[col.dataKey]}
                onChange={handleInputChange}
              />
            </Form.Field>
          );
      }
    });
    return fields;
  };

  return (
    <>
      <Modal
        onClose={() => dispatch({ type: "CLOSE_EDITFORM" })}
        open={state.isEditFormOpen}
        closeIcon
      >
        <Modal.Header></Modal.Header>
        <Modal.Content>
          <Form>{formGroups(2)}</Form>
        </Modal.Content>
        <Modal.Actions>
          <Button
            primary
            onClick={() =>
              dispatch({
                type: state.editedRowIndex == -1 ? "CREATE" : "UPDATE",
                payload: { row },
              })
            }
          >
            
          </Button>
          <Button
            floated="left"
            color="red"
            onClick={() => {
              if (!confirm("?")) return;
              dispatch({ type: "DELETE", payload: { id: row.id } });
            }}
          >
            
          </Button>
        </Modal.Actions>
      </Modal>
    </>
  );
}
