import { useState } from "react";
import {
  Table,
  TableHeader,
  TableHeaderCell,
  TableBody,
  TableRow,
  TableCell,
  Label,
  Rating,
  Header,
  HeaderContent,
  HeaderSubheader,
  Button,
  Icon,
} from "semantic-ui-react";
import { v4 as uuidv4 } from "uuid";

export default function DataTable() {
  // 
  const [column, setColumn] = useState(null);
  const [direction, setDirection] = useState("ascending");
  const [data, setData] = useState([
    {
      name: "",
      score: 1,
    },
    {
      name: "",
      score: 30,
    },
    {
      name: "",
      score: 4,
    },
    {
      name: "",
      score: 2,
    },
  ]);

  const sortData = (actionColumn) => {
    // 
    if (column == actionColumn) {
      setData(data.slice().reverse());
      // 
      setDirection(direction == "descending" ? "ascending" : "descending");
    } else {
      // 
      setData(
        data
          .slice()
          .sort((a, b) => (a[actionColumn] > b[actionColumn] ? 1 : -1))
      );
      setDirection("ascending");
      setColumn(actionColumn);
    }
  };

  return (
    <>
      <Table sortable>
        <TableHeader>
          <TableRow>
            <TableHeaderCell
              width={2}
              onClick={() => sortData("name")}
              sorted={column == "name" ? direction : null}
            >
              
            </TableHeaderCell>
            <TableHeaderCell
              width={2}
              onClick={() => sortData("score")}
              sorted={column == "score" ? direction : null}
            >
              
            </TableHeaderCell>
          </TableRow>
        </TableHeader>
        <TableBody>
          {data.map((obj) => (
            <TableRow key={uuidv4()}>
              <TableCell>{obj.name}</TableCell>
              <TableCell>{obj.score}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </>
  );
}
