// 
// import { read, create, update, destory } from "./db/pdo";
import { read, create, update, destory } from "./db/firebase";

export const reducer = async (state, action) => {
  //  row (create,update)
  const row = action.payload?.row;

  let id = "";

  

  // 
  switch (action.type) {
    // 
    case "ADD_OPTION":
      const value = action.value;
      id = await addOption(value);
      console.log(id)
      state.options.unshift({
        text: value,
        key: value,
        value: value,
      });

      return {
        ...state,
      };

    // 
    case "LOAD":
      const data = await read();
      // const options = await readOptions();
      
      return {
        ...state,
        data,
        // options,
        loading: false,
      };

    // 
    case "ADD":
      return {
        ...state,
        editedRowIndex: -1,
        isEditFormOpen: true,
      };
    // 
    case "EDIT":
      return {
        ...state,
        editedRowIndex: action.payload.index,
        isEditFormOpen: true,
      };

    // 
    case "CLOSE_EDITFORM":
      return {
        ...state,
        isEditFormOpen: false,
      };

    // 
    case "CREATE":
      id = await create(row);

      //  id ,  row 
      state.data.unshift({ ...row, id });
      return {
        ...state,
        isEditFormOpen: false,
        editedRowIndex: -1,
      };

    // 
    case "UPDATE":
      await update(row);
      Object.assign(state.data[state.editedRowIndex], row);

      return {
        ...state,
        isEditFormOpen: false,
        editedRowIndex: -1,
      };

    // 
    case "DELETE":
      id = action.payload.id;
      await destory(id);
      return {
        ...state,
        data: state.data.filter((obj) => obj.id != id),
        isEditFormOpen: false,
        editedRowIndex: -1,
      };

    // 
    case "SORT":
      let direction = "ascending";
      // 
      let sortedData = state.data;
      const columnName = action.payload.sortedColumn;
      const columnType = action.payload.type;

      if (state.sortedColumn == columnName) {
        direction = state.direction == "ascending" ? "descending" : "ascending";
        sortedData = state.data.slice().reverse();
      } else {
        direction = "ascending";
        sortedData = state.data.slice().sort((a, b) => {
          // 
          if (columnType == "number")
            return a[columnName] * 1 > b[columnName] * 1 ? 1 : -1;
          // 
          return a[columnName] > b[columnName] ? 1 : -1;
        });
      }

      return {
        ...state,
        data: sortedData,
        // 
        sortedColumn: columnName,
        direction,
      };
  }
};
