import {
  Table,
  TableHeader,
  TableHeaderCell,
  TableBody,
  TableRow,
  TableCell,
  Label,
  Rating,
  Header,
  HeaderContent,
  HeaderSubheader,
  Button,
  Icon,
} from "semantic-ui-react";
// import People from "../Label";

import "./style.css";
// import { data } from "react-router-dom";

export default function index() {
  return (
    // basic='very' 
    <Table basic="very" className="basic" unstackable striped padded>
      <TableHeader>
        <TableRow>
          <TableHeaderCell></TableHeaderCell>
          <TableHeaderCell></TableHeaderCell>
          <TableHeaderCell></TableHeaderCell>
          <TableHeaderCell>
            <Button primary icon>
              <Icon name="plus" />
            </Button>
          </TableHeaderCell>
        </TableRow>
      </TableHeader>

      <TableBody>
        <TableRow>
          <TableCell>11-15</TableCell>
          <TableCell></TableCell>
          <TableCell></TableCell>
          <TableCell>
            {" "}
            <Button icon onClick={() => handleEdit(row, index)}>
              <Icon name="pencil" />
            </Button>
          </TableCell>
        </TableRow>
        <TableRow className="abc">
          <TableCell>11-08</TableCell>
          <TableCell></TableCell>
          <TableCell>
            <Header as="h4">
              <HeaderContent>Lena</HeaderContent>
            </Header>
          </TableCell>
          <TableCell></TableCell>
        </TableRow>
        <TableRow>
          <TableCell>10-06</TableCell>
          <TableCell></TableCell>
          <TableCell>
            <Rating icon="star" defaultRating={3} maxRating={3} />
          </TableCell>
        </TableRow>
        <TableRow>
          <TableCell>08-23</TableCell>
          <TableCell></TableCell>
          <TableCell>
            100% <br />
            <a href="#">65 studies</a>
          </TableCell>
          <TableCell> </TableCell>
        </TableRow>
      </TableBody>
    </Table>
  );
}
